"use client";

import { useEffect, useState, useRef } from "react";
import Link from "next/link";

export default function CosmicVoiceCommand() {
  const [totalScore, setTotalScore] = useState(0);
  const [missionsComplete, setMissionsComplete] = useState(0);
  const [isClient, setIsClient] = useState(false);
  const [zorblaxMessage, setZorblaxMessage] = useState("Oi, space cadet! I’m Zorblax. Use your voice or cam to command—don’t mess it up!");
  const [currentMission, setCurrentMission] = useState(0);
  const [recording, setRecording] = useState(false);
  const [gestureMode, setGestureMode] = useState(false);
  const [timeLeft, setTimeLeft] = useState(15);
  const mediaRecorderRef = useRef(null);
  const videoRef = useRef(null);
  const streamRef = useRef(null);
  const timerRef = useRef(null);
  const handTrackRef = useRef(null);

  const missions = [
    {
      scenario: "Your ship’s stuck in a wormhole. Command it to escape!",
      voiceCommand: "activate warp drive",
      gesture: "salute", // One hand raised near face
      zorblaxCorrect: "Warp drive on! We’re out, you stellar screamer!",
      zorblaxIncorrect: "What was that? Salute like you mean it, loudmouth!"
    },
    {
      scenario: "An alien ambassador is freaking out. Calm it down!",
      voiceCommand: "peace and quiet",
      gesture: "wave", // Open hand moving side to side
      zorblaxCorrect: "Nice one! Alien’s chill now—your wave’s not half bad!",
      zorblaxIncorrect: "Wave, you dunce, not flail! Try again."
    },
    {
      scenario: "A star’s about to supernova. Summon a containment field!",
      voiceCommand: "deploy containment",
      gesture: "raise both hands", // Two hands up
      zorblaxCorrect: "Field up! Star’s safe—you’re a cosmic champ!",
      zorblaxIncorrect: "Both hands up, not one! Do it right, space-brain."
    },
    {
      scenario: "The galaxy’s comms are down. Broadcast a signal!",
      voiceCommand: "send galactic ping",
      gesture: "point forward", // One hand extended forward
      zorblaxCorrect: "Ping sent! Galaxy’s back online—nice point!",
      zorblaxIncorrect: "Point forward, you oaf! Not whatever that was."
    },
    {
      scenario: "A meteor storm’s incoming. Order a shield boost!",
      voiceCommand: "boost shields now",
      gesture: "cross arms", // Arms crossed over chest
      zorblaxCorrect: "Shields boosted! You’re a star, space-talker!",
      zorblaxIncorrect: "Cross your arms, not flop ‘em! Try again."
    }
  ];

  useEffect(() => {
    setIsClient(true);
    if (typeof window !== "undefined") {
      const savedScore = Number(localStorage.getItem('totalScore')) || 0;
      console.log("[next-mystery] Initial load - totalScore from LocalStorage:", savedScore);
      setTotalScore(savedScore);

      // Load handtrack.js dynamically
      const script = document.createElement("script");
      script.src = "https://cdn.jsdelivr.net/npm/handtrackjs/dist/handtrack.min.js";
      script.async = true;
      script.onload = () => {
        console.log("[next-mystery] Handtrack.js loaded");
        handTrackRef.current = window.handTrack;
      };
      document.body.appendChild(script);
    }
  }, []);

  useEffect(() => {
    if (isClient && typeof window !== "undefined") {
      console.log("[next-mystery] Saving totalScore to LocalStorage:", totalScore);
      localStorage.setItem('totalScore', totalScore);
      setMissionsComplete(missions.filter((_, idx) => idx < currentMission).length);
    }
  }, [totalScore, currentMission, isClient]);

  const startRecording = async () => {
    if (!gestureMode) {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
        mediaRecorderRef.current = new MediaRecorder(stream);
        const chunks = [];
        mediaRecorderRef.current.ondataavailable = (e) => chunks.push(e.data);
        mediaRecorderRef.current.onstop = () => {
          const blob = new Blob(chunks, { type: "audio/webm" });
          recognizeVoice(blob);
          stream.getTracks().forEach((track) => track.stop());
        };
        mediaRecorderRef.current.start();
        setRecording(true);
        startTimer();
      } catch (err) {
        setZorblaxMessage("Mic access denied, you silent fool! Try gestures instead.");
      }
    } else {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({ video: true });
        streamRef.current = stream;
        videoRef.current.srcObject = stream;
        videoRef.current.play();
        setRecording(true);
        startTimer();
        startGestureDetection();
      } catch (err) {
        setZorblaxMessage("Cam access denied, you invisible twit! Use your voice.");
      }
    }
  };

  const stopRecording = () => {
    if (!gestureMode && mediaRecorderRef.current) {
      mediaRecorderRef.current.stop();
    } else if (gestureMode && streamRef.current) {
      streamRef.current.getTracks().forEach((track) => track.stop());
      stopGestureDetection();
    }
    setRecording(false);
    clearTimeout(timerRef.current);
  };

  const startTimer = () => {
    setTimeLeft(15);
    timerRef.current = setInterval(() => {
      setTimeLeft((prev) => {
        if (prev <= 1) {
          stopRecording();
          setZorblaxMessage("Time’s up, slowpoke! Try again—15 seconds max!");
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
  };

  const recognizeVoice = (blob) => {
    const command = missions[currentMission].voiceCommand.toLowerCase();
    const reader = new FileReader();
    reader.onload = () => {
      const text = "mock voice data"; // Placeholder
      if (text.includes(command.split(" ")[0]) || text.includes(command.split(" ")[1])) {
        completeMission();
      } else {
        setZorblaxMessage(missions[currentMission].zorblaxIncorrect);
      }
    };
    reader.readAsDataURL(blob);
  };

  const startGestureDetection = () => {
    if (!handTrackRef.current) {
      setZorblaxMessage("Gesture system not ready—wait a sec or use voice!");
      return;
    }
    handTrackRef.current.startVideo(videoRef.current).then((status) => {
      if (status) {
        console.log("[next-mystery] Video started for gesture detection");
        detectGesture();
      } else {
        setZorblaxMessage("Video failed to start—check your cam, space cadet!");
      }
    });
  };

  const stopGestureDetection = () => {
    if (handTrackRef.current) {
      handTrackRef.current.stop();
    }
  };

  const detectGesture = () => {
    const modelParams = {
      flipHorizontal: true,
      maxNumBoxes: 2,
      iouThreshold: 0.5,
      scoreThreshold: 0.6
    };
    handTrackRef.current.load(modelParams).then((model) => {
      const detectInterval = setInterval(() => {
        model.detect(videoRef.current).then((predictions) => {
          if (recording) {
            const gesture = missions[currentMission].gesture.toLowerCase();
            let matched = false;

            if (predictions.length > 0) {
              const hands = predictions.filter((p) => p.label === "open" || p.label === "closed");
              if (gesture === "salute" && hands.length === 1 && hands[0].bbox[1] < 150) {
                matched = true; // Hand near top (face level)
              } else if (gesture === "wave" && hands.length === 1) {
                matched = true; // Single open hand
              } else if (gesture === "raise both hands" && hands.length === 2) {
                matched = true; // Two hands detected
              } else if (gesture === "point forward" && hands.length === 1 && hands[0].bbox[2] > 100) {
                matched = true; // Wide hand (extended)
              } else if (gesture === "cross arms" && hands.length === 0) {
                matched = true; // No hands visible (arms crossed assumption)
              }
            }

            if (matched) {
              clearInterval(detectInterval);
              stopRecording();
              completeMission();
            }
          }
        });
      }, 500); // Check every 500ms
      timerRef.current.detectInterval = detectInterval; // Store to clear later
    });
  };

  const completeMission = () => {
    setTotalScore((prev) => {
      const newScore = prev + 20;
      localStorage.setItem('totalScore', newScore);
      return newScore;
    });
    setZorblaxMessage(missions[currentMission].zorblaxCorrect);
    const audio = new Audio("/sounds/mission.mp3");
    audio.play().catch(() => console.log("[next-mystery] Audio missing"));
    setCurrentMission((prev) => prev + 1);
  };

  if (!isClient) {
    return <div style={{ textAlign: "center", padding: "2rem", color: "#fff" }}>Loading...</div>;
  }

  return (
    <div style={{
      padding: "2rem",
      textAlign: "center",
      background: "linear-gradient(135deg, #001a33, #003366)",
      minHeight: "100vh",
      color: "#e0e0e0",
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      backgroundImage: "radial-gradient(circle, rgba(0, 204, 255, 0.1) 2px, transparent 2px)",
      backgroundSize: "25px 25px"
    }}>
      <h1 style={{
        fontSize: "clamp(2rem, 8vw, 4rem)",
        color: "#00ccff",
        fontFamily: "'Courier New', monospace",
        textShadow: "0 0 10px #00ccff, 0 0 20px #00ccff",
        marginBottom: "1rem",
        animation: "pulse 1.5s infinite"
      }}>Cosmic Voice Command</h1>
      <div style={{
        fontSize: "clamp(1.5rem, 5vw, 2rem)",
        color: "#fff",
        background: "rgba(0, 204, 255, 0.2)",
        padding: "0.5rem 1rem",
        borderRadius: "15px",
        border: "2px solid #00ccff",
        display: "inline-block",
        animation: "shine 2s infinite",
        marginBottom: "1rem"
      }}>
        Total Score: {totalScore}
      </div>
      <p style={{
        fontSize: "clamp(1rem, 3vw, 1.2rem)",
        color: "#00ccff",
        margin: "0 0 1rem",
        fontFamily: "'Courier New', monospace",
        maxWidth: "90%"
      }}>Speak or gesture to command—Zorblax demands precision in 15 seconds!</p>
      <div style={{
        width: "100%",
        maxWidth: "800px",
        padding: "1rem",
        background: "rgba(0, 204, 255, 0.1)",
        borderRadius: "15px",
        marginBottom: "2rem",
        fontSize: "clamp(1rem, 2.5vw, 1.2rem)",
        color: "#cceeff",
        fontFamily: "'Courier New', monospace",
        textAlign: "left",
        border: "1px dashed #00ccff"
      }}>
        <strong>Zorblax:</strong> {zorblaxMessage}
      </div>
      {currentMission < 5 ? (
        <div style={{
          width: "100%",
          maxWidth: "600px",
          padding: "1.5rem",
          background: "rgba(0, 51, 102, 0.8)",
          borderRadius: "15px",
          boxShadow: "0 0 20px rgba(0, 204, 255, 0.5)",
          border: "2px solid #00ccff"
        }}>
          <h2 style={{
            fontSize: "clamp(1.5rem, 4vw, 2rem)",
            color: "#00ccff",
            marginBottom: "1rem",
            textShadow: "0 0 5px #00ccff"
          }}>Mission {currentMission + 1}</h2>
          <p style={{
            fontSize: "clamp(1rem, 3vw, 1.2rem)",
            marginBottom: "1rem",
            color: "#99ccff"
          }}>{missions[currentMission].scenario}</p>
          <p style={{
            fontSize: "clamp(0.9rem, 2.5vw, 1.1rem)",
            color: "#fff",
            marginBottom: "1rem"
          }}>
            {gestureMode
              ? `Gesture: ${missions[currentMission].gesture}`
              : `Say: "${missions[currentMission].voiceCommand}"`}
          </p>
          <p style={{
            fontSize: "clamp(0.9rem, 2.5vw, 1.1rem)",
            color: "#fff",
            marginBottom: "1rem"
          }}>Time Left: {timeLeft}s</p>
          <div style={{
            display: "flex",
            flexDirection: "column",
            gap: "1rem",
            alignItems: "center"
          }}>
            <button
              onClick={() => setGestureMode(!gestureMode)}
              style={{
                padding: "clamp(0.5rem, 2vw, 0.75rem) clamp(1rem, 3vw, 1.5rem)",
                fontSize: "clamp(1rem, 2.5vw, 1.2rem)",
                background: gestureMode ? "#ff4444" : "#00ccff",
                color: "#fff",
                border: "none",
                borderRadius: "10px",
                cursor: "pointer",
                width: "min(100%, 200px)"
              }}
            >
              {gestureMode ? "Switch to Voice" : "Switch to Gesture"}
            </button>
            <button
              onClick={recording ? stopRecording : startRecording}
              style={{
                padding: "clamp(0.5rem, 2vw, 0.75rem) clamp(1rem, 3vw, 1.5rem)",
                fontSize: "clamp(1rem, 2.5vw, 1.2rem)",
                background: recording ? "#ff4444" : "#00ccff",
                color: "#fff",
                border: "none",
                borderRadius: "10px",
                cursor: "pointer",
                width: "min(100%, 200px)",
                boxShadow: recording ? "0 0 15px #ff4444" : "0 0 10px #00ccff"
              }}
            >
              {recording ? "Stop" : gestureMode ? "Start Gesture" : "Start Recording"}
            </button>
          </div>
          {gestureMode && (
            <video
              ref={videoRef}
              autoPlay
              muted
              style={{
                width: "min(100%, 320px)",
                height: "min(75vw, 240px)",
                marginTop: "1rem",
                border: "2px solid #00ccff",
                borderRadius: "10px",
                transform: "scaleX(-1)" // Mirror effect
              }}
            />
          )}
        </div>
      ) : (
        <div style={{
          marginTop: "2rem",
          display: "flex",
          flexDirection: "column",
          gap: "1rem",
          alignItems: "center"
        }}>
          <p style={{
            fontSize: "clamp(1.5rem, 4vw, 2rem)",
            color: "#ffd700",
            textShadow: "0 0 10px #ffd700"
          }}>All Missions Complete!</p>
          <Link href="/finale">
            <button style={{
              padding: "clamp(0.5rem, 2vw, 0.75rem) clamp(1rem, 3vw, 1.5rem)",
              fontSize: "clamp(1rem, 2.5vw, 1.2rem)",
              background: "linear-gradient(45deg, #00ccff, #ff00ff)",
              color: "#fff",
              border: "none",
              borderRadius: "10px",
              cursor: "pointer",
              boxShadow: "0 0 15px #00ccff",
              animation: "glow 1.5s infinite alternate"
            }}>
              Next Cosmic Caper!
            </button>
          </Link>
        </div>
      )}
      <style jsx global>{`
        @keyframes pulse {
          0% { transform: scale(1); }
          50% { transform: scale(1.05); }
          100% { transform: scale(1); }
        }
        @keyframes shine {
          0% { text-shadow: 0 0 5px #00ccff, 0 0 10px #00ccff, 0 0 15px #fff; }
          50% { text-shadow: 0 0 10px #00ccff, 0 0 20px #00ccff, 0 0 30px #fff; }
          100% { text-shadow: 0 0 5px #00ccff, 0 0 10px #00ccff, 0 0 15px #fff; }
        }
        @keyframes glow {
          0% { box-shadow: 0 0 10px #00ccff; }
          100% { box-shadow: 0 0 20px #ff00ff; }
        }
        @media (max-width: 600px) {
          .mission-container {
            padding: 1rem;
          }
        }
      `}</style>
    </div>
  );
}